.NavbarItems{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    padding:0 30px;
    background-color: #222;
    box-shadow: 0 5px 15px rgba(0,0,0,0.75);
    width:90%;
    height: 80px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    transition-timing-function: ease-out;
    transition:0.5s;
}
.Navhide{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    padding:0 30px;
    background-color: #222;
    box-shadow: 0 5px 15px rgba(0,0,0,0.75);
    width:90%;
    height: 80px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    transition-timing-function: ease-in;
    transition: 0.5s;
    opacity: 0;
}




.logo{
    color: #fff;
    justify-self: start;
    cursor: pointer;
}



.nav-menu{
    display: grid;
    grid-template-columns: repeat(7,auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
    text-align: center;
    justify-content: end;
}

.nav-links{
    text-decoration: none;
    color: #fff;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.nav-links i{
    padding-right: 10px;
}

.nav-links:hover{
    background-color: rgb(188, 129, 129);
    color: #222;
    border-radius: 25px;
    transition: all 0.2s ease-in-out;
}

.icons{
    color: #fff;
}

.menu-icons{
    display: none;
}


@media screen and (max-width: 850px){
    .NavbarItems{
        z-index:99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: rgba(255, 255, 255, 0.457);
        box-shadow: 0 8px 32px 0 rgba(31,38,135,.37);
        backdrop-filter: blur(4px);
        border-radius: 13px;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -110%;
        align-items: stretch;
        padding:80px 0 30px 0;
        margin:0;
        z-index: -1;
        transition: all 0.3s ease-in-out;
    }
    .nav-menu.active{
        left:0%;
    }
    .nav-links{
        display:block;
        widows:100%;
        padding: 2rem 0;
        color: #222;
    }
    .nav-links:hover{
        background: rgba(255,255,255,0.25);
        backdrop-filter: blur(20px);
    }
    .menu-icons{
        display: block;
    }
}